<template>
  <v-card color="transparent" flat>
    <v-row>
      <v-col cols="12" md="4">
        <v-card-text class="pa-0 pb-6 white--text text-body-1">
          <v-container class="my-container pa-6">
            <v-form ref="form" v-model="valid" lazy-validation>
              <v-row>
                <v-col cols="12"
                  ><span class="text-h6">短期職缺是否符合法規</span></v-col
                >
              </v-row>
              <v-row class="d-flex align-center">
                <v-col cols="12">職缺名稱：</v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="jobTitle"
                    placeholder="請輸入職缺名稱"
                    :rules="rules"
                    class="input-warning"
                    dense
                    outlined
                    hide-details
                    single-line
                    filled
                    dark
                  >
                  </v-text-field>
                </v-col>
              </v-row>
              <v-row class="d-flex align-center">
                <v-col cols="12">職缺的契約期限：{{ dateDiff }} 天</v-col>
                <v-col cols="12">
                  <v-menu
                    v-model="menu"
                    :close-on-content-click="false"
                    transition="scale-transition"
                    offset-y
                    min-width="auto"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="dateRangeText"
                        append-icon="mdi-calendar"
                        placeholder="請選擇日期區間"
                        :rules="rules"
                        class="input-warning"
                        @click="show"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                        dense
                        outlined
                        hide-details
                        single-line
                        filled
                        dark
                      ></v-text-field>
                    </template>
                    <DatePicker
                      :value="null"
                      v-model="dateRange"
                      color="red"
                      is-range
                      trim-weeks
                    />
                  </v-menu>
                </v-col>
              </v-row>
              <v-row class="d-flex align-center">
                <v-col cols="6" class="pe-0">職缺是否已完成招募？</v-col>
                <v-col cols="6" class="ps-0 d-flex justify-end">
                  <!-- <div>
                    <v-btn class="ma-0" color="primary" text tile>
                      <input type="radio" name="jobDone" class="d-none" />
                      <span>是</span>
                    </v-btn>
                    <label class="pa-3 my-radio pointer">
                      <input type="radio" name="jobDone" class="d-none" />
                      <span>否</span>
                    </label>
                  </div> -->
                  <v-btn-toggle
                    v-model="jobDone"
                    color="primary"
                    borderless
                    group
                    dark
                  >
                    <v-btn value="1" class="mx-0"> 是 </v-btn>
                    <v-btn value="0" class="mx-0"> 否 </v-btn>
                  </v-btn-toggle>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
      </v-col>
      <v-col cols="12" md="8">
        <v-card-text class="pa-0 pb-6 white--text text-body-1">
          <v-container class="my-container pa-6">
            <v-row
              v-for="(i, index) in question"
              :key="index"
              class="d-flex align-center"
            >
              <v-col
                cols="9"
                class="flex-grow-1 flex-shrink-0"
                style="min-width: 100px; max-width: 100%"
              >
                <v-row>
                  <v-col cols="auto" class="flex-grow-0 flex-shrink-0 pe-0"
                    >{{ index + 1 }}.</v-col
                  >
                  <v-col
                    cols="10"
                    class="flex-grow-1 flex-shrink-0"
                    style="min-width: 100px; max-width: 100%"
                    >{{ i.q }}</v-col
                  >
                </v-row>
              </v-col>
              <v-col
                cols="12"
                md="auto"
                class="flex-grow-0 flex-shrink-1 d-flex justify-end"
              >
                <v-btn-toggle
                  v-model="i.id"
                  color="primary"
                  borderless
                  group
                  dark
                >
                  <v-btn value="1" class="mx-0"> 是 </v-btn>
                  <v-btn value="0" class="mx-0"> 否 </v-btn>
                </v-btn-toggle>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-col>
    </v-row>
    <v-row class="d-flex justify-center">
      <v-col cols="12" md="8">
        <v-card-actions class="px-0">
          <v-container>
            <v-row>
              <v-col cols="6" class="ps-0">
                <v-btn
                  color="rgba(0, 0, 0, 0.36)"
                  class="py-6 my-btn"
                  depressed
                  block
                  dark
                  @click="next"
                  >進行分析</v-btn
                >
              </v-col>
              <v-col cols="6" class="pe-1">
                <v-btn
                  color="rgba(0, 0, 0, 0.36)"
                  class="py-6 my-btn"
                  depressed
                  block
                  dark
                  @click="prev"
                  >返回上一步</v-btn
                ></v-col
              >
            </v-row>
          </v-container>
        </v-card-actions>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import moment from "moment";
import DatePicker from "v-calendar/lib/components/date-picker.umd";

export default {
  name: "",
  components: { DatePicker },
  data: () => ({
    valid: true,
    rules: [(v) => !!v || ""],
    step: 2,
    menu: false,
    dateRange: {
      start: null,
      end: null,
    },
    dateRangeText: "",
    dateDiff: 0,
    jobTitle: "",
    jobDone: null,
    question: [
      {
        id: "q1",
        q: "職缺任職者是否長期派駐另一間公司或單位，以擔任該處的派遣人員？",
      },
      { id: "q2", q: "職缺任職者是否為留職停薪或長期休假員工的職務代理人？" },
      { id: "q3", q: "職缺是否為一年一聘，每年約滿時視情況續聘？" },
      {
        id: "q4",
        q: "職缺的職務工作是否為貴單位的常態性業務 (而非偶發性業務，例如臨時活動或專案計畫) ？",
      },
      { id: "q5", q: "契約期滿後，職缺的職務工作是否須有人接手繼續進行？" },
      { id: "q5", q: "是否有正職員工從事相同的職務工作？" },
    ],
    q1: null,
  }),
  watch: {
    dateRange() {
      const start = this.dateRange.start;
      const end = this.dateRange.end;
      this.dateDiff = parseInt(Math.abs(end - start) / 1000 / 60 / 60 / 24) + 1;
      this.dateRangeText =
        moment(start).format("YYYY-MM-DD") +
        " ～ " +
        moment(end).format("YYYY-MM-DD");
      this.menu = false;
    },
  },
  methods: {
    show() {
      this.menu = true;
    },
    next() {
      if (this.$refs.form.validate()) {
        const params = {
          jobTitle: this.jobTitle.trim(),
          dateDiff: this.dateDiff,
          dateRangeText: this.dateRangeText,
        };
        this.$emit("next", params);
      } else {
        this.$nextTick(() => {
          const el = this.$el.querySelector(
            ".v-input--has-state.error--text:first-of-type"
          );
          this.$vuetify.goTo(el);
          return;
        });
      }
    },
    prev() {
      this.$emit("prev", 1);
    },
  },
};
</script>

<style scoped>
.pointer {
  cursor: pointer;
}

.my-container {
  color: #eeeeee;
  background: rgba(0, 0, 0, 0.12);
  border-radius: 8px;
}

.my-btn {
  box-shadow: 1px 1px 0px hsl(0, 0%, 5%), 2px 2px 0px hsl(0, 0%, 5%),
    3px 3px 0px hsl(0, 0%, 5%);
}

.my-radio {
  background: rgba(255, 255, 255, 0.12);
  color: #ff5d3d;
}
